import { NextFunction, Request, Response, Router } from 'express';
import { BaseApiRoute } from '../API_Route';


/**
 * / route
 *
 * @class User
 */
export class IndexApiRoute extends BaseApiRoute {

  /**
   * Create the routes.
   *
   * @class IndexApiRoute
   * @method create
   * @static
   */
  public static create(router: Router) {
    //log
    console.log('[IndexRoute::create] Creating index api route.');

    //add home page route
    router.get('/', (req: Request, res: Response, next: NextFunction) => {
      new IndexApiRoute().process(req, res, next);
    });
  }

  /**
   * Constructor
   *
   * @class IndexRoute
   * @constructor
   */
  constructor() {
    super();
  }

  /**
   * The home page route.
   *
   * @class IndexRoute
   * @method index
   * @param req {Request} The express Request object.
   * @param res {Response} The express Response object.
   * @next {NextFunction} Execute the next method.
   */
  public process(req: Request, res: Response, next: NextFunction) {
    let result = {
      data: 'Api route testing',
      api_code: '0',
      error_msg: ''
    };
    this.api_response(res, result);
  }
}